body {
  font-family: "Lato", sans-serif;
  background-image: url("../../public/cab_pic_ai.png");
}

.sidebar {
  height: 100%;
  width: 250px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(255, 255, 83);
  opacity: 0.9;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
  display: block;
}

.sidebar div {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: black;
  display: block;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  /* Added box-shadow */
  cursor: pointer;
}

.sidebar div:hover {
  color: rgb(85, 85, 255);
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  cursor: pointer;
}


.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: rgb(255, 255, 83);
  color: black;
  padding: 10px 15px;
  border: 10px;
  border-bottom-right-radius: 50%;
  border-top-right-radius: 50%;
}

.openbtn:hover {
  background-color: #444;
  color: beige;
}


#main {
  transition: margin-left .5s;
  padding: 16px;
}

div.subMenuOptions{
  font-size:18px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-width:1250px) {
  .sidebar {
    width: 100%;
    text-align: center;
    display: block;
  }

  .sidebar button {
    font-size: 18px;
  }
}


